:root {
  --header-font-family: "Alegreya", serif;
  --header-text-transform: uppercase;
  --header-font-weight: bold;
  --header-navigation-hover: #ffaa00;
  --button-sign-background: #124c5f;
  --button-register-background: #c7e7e1;
  --button-register-color: #124c5f;
}

@media (min-width: 485px) and (max-width: 1080px) {
  .header .navigationBurger {
    display: none;
  }
}

@media (min-width: 315px) and (max-width: 480px) {
  .header .navigation {
    display: none;
  }

  .header .auth .sign,
  .header .auth .register {
    display: none;
  }
}

@media (min-width: 315px) and (max-width: 1080px) {
  .header {
    z-index: 1;
    height: 150px;
    grid-template-columns: 100%;
    grid-template-rows: 10% 50% 40%;
  }

  .header .navigation nav {
    flex-wrap: wrap;
  }

  .header .navigation nav a {
    margin-top: 5px;
  }

  .header .logo {
    justify-content: flex-start;
  }

  .header .auth {
    justify-content: flex-start;
  }
}

@media (min-width: 750px) and (max-width: 1080px) {
  .header {
    grid-template-rows: 3fr 3fr 4fr;
  }

  .header .navigation nav {
    justify-content: space-around;
  }
}

@media (min-width: 1085px) and (max-width: 12450px) {
  .header {
    grid-template-columns: 20% 50% 30%;
  }

  .header .auth {
    justify-content: space-evenly;
  }

  .header .navigation nav {
    justify-content: space-between;
  }

  .header .navigationBurger {
    display: none;
  }

  .header .logo {
    justify-content: flex-end;
  }
}

.header {
  height: 104px;
  display: grid;
  align-items: center;
}

.header .logo {
  display: flex;
}

.header .hamburgerMenu {
  display: block;
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  cursor: pointer;
}

.header .hamburgerMenu div {
  width: 80px;
  height: 8px;
  border-radius: 4px;
  margin-bottom: 15px;
  background-color: #000;
  display: inline-block;
}

.header .hamburgerMenu .change1 {
  animation: stick-1-open 0.6s ease-out forwards;
}

.header .hamburgerMenu .change2 {
  animation: stick-2-open 0.6s linear forwards;
}

.header .hamburgerMenu .change3 {
  animation: stick-3-open 0.6s linear forwards;
}

@keyframes stick-1-open {
  0% {
    width: 80px;
  }
  40% {
    background-color: #ffad14;
    width: 8px;
    transform: translate(40px, 0px);
  }
  75%,
  80% {
    width: 8px;
    transform: translate(40px, -50px);
    animation-timing-function: cubic-bezier(0, 1, 1, 1);
  }
  100% {
    background-color: #ffad14;
    width: 8px;
    transform: translate(35px, 46px);
  }
}

@keyframes stick-2-open {
  80% {
    background-color: #fdd32d;
    transform: translate(0px, 0px) rotate(0deg);
  }
  100% {
    background-color: #ff3f14;
    transform: translate(8px, 0px) rotate(40deg);
  }
}

@keyframes stick-3-open {
  80% {
    background-color: #fdef2d;
    transform: translate(0px, 0px) rotate(0deg);
  }
  100% {
    background-color: #ff4714;
    transform: translate(8px, -23px) rotate(-40deg);
  }
}

.header .navigation nav {
  display: flex;
  margin: auto;
}

.header .navigation a {
  white-space: nowrap;
  font-family: var(--header-font-family);
  text-transform: var(--header-text-transform);
  font-weight: var(--header-font-weight);
  font-size: 100%;
  text-decoration: none;

  color: black;
}

.header .navigation a:hover {
  color: var(--header-navigation-hover);
}

.header .navigationBurger {
  z-index: 2;
  width: 100%;
}

.header .navigationBurger nav ul {
  width: 100%;
  padding: 0;
}

.header .navigationBurger nav ul .link {
  border: 1px solid rgb(97, 96, 96);
  list-style-type: none;
  padding: 10px 0px;
  margin: 0;
  font-weight: bold;

  background-color: rgba(255, 255, 255, 0.893);
}

.header .navigationBurger nav ul .link a {
  text-decoration: none;
  color: #000;
}

.header .navigationBurger nav ul .link a:hover {
  color: var(--header-navigation-hover);
}

.header .auth {
  display: flex;
  align-content: center;
  align-items: center;
  height: 104px;
  flex-direction: row;
}

.header .auth .sign {
  min-width: 70px;
  width: 35%;
  max-width: 150px;
  height: 50px;
  cursor: pointer;

  font-family: var(--header-font-family);
  text-transform: var(--header-text-transform);
  font-weight: var(--header-font-weight);

  color: #ffffff;
  background: var(--button-sign-background);
  border: none;
  border-radius: 5px;
}

.header .auth .sign:hover,
.header .auth .register:hover {
  text-decoration: underline;
}

.header .auth .register {
  min-width: 70px;
  width: 35%;
  max-width: 150px;
  height: 50px;
  cursor: pointer;

  font-family: var(--header-font-family);
  text-transform: var(--header-text-transform);
  font-weight: var(--header-font-weight);

  color: var(--button-register-color);
  background: var(--button-register-background);
  border: none;
  border-radius: 5px;
}

.header .navigationBurger .burgerActive {
  display: block;
  margin-top: 140%;
  z-index: 2;
}

.header .navigationBurger .burgerHide {
  display: none;
}
